module.exports = "import React, { Component } from 'react';\nimport jss from 'jss';\nimport preset from 'jss-preset-default';\nimport Logo from './logo';\n\njss.setup(preset());\n\nconst styles = {\n  container: {\n    display: 'flex',\n    flexDirection: 'column',\n    minHeight: '100%',\n    width: '100%',\n    backgroundColor: '#f6f9fc'\n  },\n  header: {\n    display: 'flex',\n    flexDirection: 'column',\n    height: '40%',\n    minHeight: 250,\n    width: '100%',\n    position: 'relative',\n    justifyContent: 'center'\n  },\n  logoContainer: {\n    display: 'flex',\n    justifyContent: 'center',\n    alignItems: 'center',\n    height: 60,\n    width: 60,\n    borderRadius: 60,\n    backgroundColor: 'white',\n    boxShadow: { x: 0, y: 1, blur: 6, color: 'rgba(0, 0, 0, 0.1)' },\n    position: 'absolute',\n    top: '1rem',\n    left: '1rem',\n    zIndex: 2\n  },\n  stripes: {\n    position: 'absolute',\n    top: 0,\n    right: 0,\n    bottom: 0,\n    left: 0,\n    width: '100%',\n    height: '100%',\n    overflow: 'hidden',\n    transform: 'skewY(-8deg)',\n    transformOrigin: 0,\n    background: 'linear-gradient(-150deg, #25b57f 0%, #acf0b5 70%)'\n  },\n  stripe: {\n    height: '10vh',\n    overflow: 'hidden',\n    transform: 'skewY(-8deg)',\n    transformOrigin: 0,\n    background:\n      'linear-gradient(-150deg, rgba(255, 255, 255, 0) 40%, #ddecf7 70%)'\n  },\n  stripeBottom: {\n    position: 'absolute',\n    bottom: 0,\n    left: 0,\n    right: 0,\n    height: '35%',\n    overflow: 'hidden',\n    background:\n      'linear-gradient(150deg, rgba(172, 240, 181, 0) 40%, #acf0b5 70%)'\n  },\n  titleContainer: {\n    padding: '1rem'\n  },\n  title: {\n    color: 'white',\n    margin: 0,\n    padding: 0,\n    position: 'relative',\n    fontSize: '1.5rem',\n    fontWeight: '100'\n  },\n  subTitle: {\n    extend: 'title',\n    color: '#025450',\n    marginTop: '0.5rem'\n  },\n  form: {\n    display: 'flex',\n    flexDirection: 'column',\n    alignItems: 'center',\n    justifyContent: 'center',\n    width: '100%',\n    padding: '2rem',\n    boxSizing: 'border-box'\n  },\n  input: {\n    display: 'block',\n    width: '100%',\n    marginBottom: '1rem',\n    padding: ['1.25rem', '1rem'],\n    boxSizing: 'border-box',\n    borderRadius: '0.25rem',\n    border: [1, 'solid', 'transparent'],\n    boxShadow: { x: 0, y: 1, blur: 6, color: 'rgba(0, 0, 0, 0.1)' },\n    '&:focus': {\n      borderColor: '#6772e5',\n      outline: 'none',\n      boxShadow: { x: 0, y: 1, blur: 6, color: 'rgba(103, 114, 229, 0.5)' }\n    }\n  },\n  submitButton: {\n    display: 'block',\n    backgroundColor: '#6772e5',\n    color: 'white',\n    border: 'none',\n    width: '100%',\n    padding: '1.25rem 1rem',\n    boxSizing: 'border-box',\n    borderRadius: '0.25rem',\n    textTransform: 'uppercase',\n    boxShadow: '0 1px 6px rgba(0, 0, 0, 0.1)',\n    marginTop: '1rem'\n  },\n  submitButtonDisabled: {\n    backgroundColor: '#BBB'\n  }\n};\n\nconst { classes } = jss.createStyleSheet(styles).attach();\n\nexport default class Login extends Component {\n  constructor(props) {\n    super(props);\n    this.state = {\n      email: '',\n      phoneNumber: '',\n      fields: ['email', 'phoneNumber'],\n      valid: false\n    };\n  }\n\n  handleInputChange(prop) {\n    return ev => {\n      const { value } = ev.target;\n      this.setState({\n        [prop]: value,\n        valid:\n          value.length > 0 &&\n          this.state.fields.every(field => this.state[field].length > 0)\n      });\n    };\n  }\n\n  render() {\n    const { valid } = this.state;\n\n    return (\n      <main className={classes.container}>\n        <header className={classes.header}>\n          <div className={classes.logoContainer}>\n            <Logo color=\"#25b57f\" size={32} />\n          </div>\n          <div className={classes.stripes}>\n            <div className={classes.stripeBottom} />\n          </div>\n          <div className={classes.titleContainer}>\n            <h1 className={classes.title}>Set up your payments</h1>\n            <h1 className={classes.subTitle}>Rocketship, Inc.</h1>\n          </div>\n        </header>\n        <div className={classes.stripe} />\n        <form className={classes.form} onSubmit={ev => ev.preventDefault()}>\n          <input\n            className={classes.input}\n            type=\"text\"\n            placeholder=\"Email\"\n            onChange={this.handleInputChange('email')}\n          />\n          <input\n            className={classes.input}\n            type=\"text\"\n            placeholder=\"Phone number\"\n            onChange={this.handleInputChange('phoneNumber')}\n          />\n          <button\n            type=\"submit\"\n            className={[\n              classes.submitButton,\n              !valid && classes.submitButtonDisabled\n            ].join(' ')}\n            disabled={!valid}\n          >\n            Submit\n          </button>\n        </form>\n      </main>\n    );\n  }\n}\n"


